// Home
import Home from './pages/home/home';
// Overlord
import Overlord from './pages/overlord/overlord';
// E-Panel
import EPanelLogin from './pages/e-panel/e-panel-login';
import PanelView from './pages/e-panel/e-panel-view';
// Error Pages
import PageNotFoundError from './pages/error/404';
// Shared Components
import { Index, Route } from './shared/components/routes/routes';


// App routes
const Routes = () => {
  return <Index>

    { // [DEFAULT] Home
      Route({
        path: '',
        app: Home
      })
    }

    { // [Overlord] Documentation
      Route({
        path: 'overlord/*',
        app: Overlord
    })}

    { // [E-Panel] User Login
      Route({
        path: 'e-panel',
        app: EPanelLogin
    })}

    { // [E-Panel] View Panel
      Route({
        path: 'e-panel/:panelID',
        app: PanelView
    })}

    { // [ERROR 404] Page not found
      Route({
        path: '*',
        app: PageNotFoundError
    })}

  </Index>
}

export default Routes;
