// React
import { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
// Assets
import './overlord.css';
// Components
import PageNotFoundError from '../error/404';
import { Link, setAppTitle } from '../../shared/components/routes/routes';


const Overlord = () => {
  const [ selectedDocument, setDocument ] = useState<string | null>(null);
  const urlPath = useLocation().pathname.split('/overlord')[1];
  const requestedDocument = urlPath === "" ? null : urlPath.split('/')[1].replaceAll("_", " ");
  if (requestedDocument !== selectedDocument) setDocument(requestedDocument)
  if (selectedDocument === null) {
    setAppTitle("Overlord");
  } else {
    setAppTitle(selectedDocument.toUpperCase() + ' | Overlord');
  }

  return <>
    <DocumentSelector
      selectedDocument={selectedDocument}
      setDocument={setDocument}
    />
    <DocumentViewer
      selectedDocument={selectedDocument}
      setDocument={setDocument}
    />
  </>
}


const DocumentSelector = ({ selectedDocument, setDocument } : any) => {
  const [ selectedDropdown, setDropdown ] = useState<string | null>(null);

  const selectDropdown = (name:string | null) => {
    if (selectedDropdown === name) setDropdown(null);
    else setDropdown(name);
  }

  const MenuButton = ({ name } : any) => <button
    onClick={() => selectDropdown(name)}
    style={selectedDropdown === name ? {
      textDecoration: 'underline',
      backgroundColor: '#1a1c20'
    } : {}}
  >
    <i className={"fa-solid fa-chevron-" + (selectedDropdown === name ? 'down' : 'right')}></i>
    {name}
  </button>

  const MenuOption = ({ option } : any) => <Link to={'overlord/' + option.toLowerCase().replaceAll(" ", "_")}>
    <div
      className="ol-document-option"
      onClick={() => {setDropdown(null);setDocument(option.capitalize());}}
    >{option}</div>
  </Link>

  const BasicOptions = () => <>
    <MenuOption option="Introduction"/>
    <MenuOption option="Getting Started"/>
    <MenuOption option="CLI Tools"/>
    <MenuOption option="E-Panel"/>
  </>

  const ClientOptions = () => <>
    <MenuOption option="Create Web Client"/>
    <MenuOption option="Create Native Client"/>
    <MenuOption option="Shared Components"/>
    <MenuOption option="Configurations"/>
    <MenuOption option="Deploy Your App"/>
  </>

  const ServerOptions = () => <>
    <MenuOption option="Built-in API"/>
    <MenuOption option="Create New API"/>
    <MenuOption option="Core Library"/>
    <MenuOption option="Unit Tests"/>
    <MenuOption option="Deploy Your Server"/>
  </>

  const MenuContainer = () => {
    return <div className="ol-document-selection">{
      selectedDropdown === "basics" ? <BasicOptions/> :
      selectedDropdown === "client" ? <ClientOptions/> :
      selectedDropdown === "server" ? <ServerOptions/> :
      <></>
    }</div>
  }

  return <>
    <div
      className="ol-document-selector-bg"
      style={selectedDropdown ? {display: 'flex'} : {display: 'none'}}
      onClick={() => {
        selectDropdown(selectedDropdown);
    }}/>
    <div className="ol-document-selector">
      <MenuButton name="basics"/>
      <MenuButton name="client"/>
      <MenuButton name="server"/>
    </div>
    { selectedDropdown === null ? <></> : <MenuContainer/> }
  </>
}


const DocumentSearch = ({ setSearching, searching } : any) => {

  useEffect(() => {
    const inputEl = document.querySelector("#ol-doc-search-input") as HTMLInputElement;
    inputEl.disabled = searching;
    if (inputEl.disabled) inputEl.style.opacity = '50%';
    else inputEl.style.opacity = '100%';
  }, [searching])

  return <div className="ol-document-search-container">
    <h1>The Overlord Documentation</h1>
    <p style={{ textAlign: 'center' }}>
      Select a category from the menu above or search for something specific below.
    </p>
    <form
      onSubmit={(e:any)=>{
        e.preventDefault();
        setSearching(true);
      }}
      className="flex-center ol-search-container"
    >
      <input id="ol-doc-search-input" className="ol-document-search" type="text" placeholder="search here..."/>
      <i className="fa-solid fa-magnifying-glass"></i>
    </form>
    <div className="flex-evenly" style={{ width: '100%', textAlign: 'center' }}>
      <p><b>Latest Version:</b>&nbsp;1.2.0</p><p><b>LTS Version:</b>&nbsp;1.0.0</p>
    </div>
  </div>
}


const DocumentSearchButton = () => {
  return <Link to="overlord">
    <div className="ol-document-search-button">
      <i className="fa-solid fa-magnifying-glass"></i>
      <p>Search</p>
    </div>
  </Link>
}


const DocumentSearchResults = ({ searching } : any) => {
  return searching ? <div className="ol-document-search-results">
    { searching ?
      <div style={{ margin: 'auto auto' }}>
        <div className="cube" style={{ margin: 'auto auto' }}>
          <div/><div/><div/><div/><div/><div/>
        </div>
        <h3 style={{ textAlign: 'center', padding: '12px 0 0 12px' }}>Searching...</h3>
      </div>
      :
      <></>
    }
  </div> : <></>
}


const DocumentViewer = ({ selectedDocument, setDocument } : any) => {
  const [ searching, setSearching ] = useState<boolean>(false);

  return <div className="ol-document-viewer">
    {
      selectedDocument === null ?
        <></>
      :
        <DocumentSearchButton/>
    }
    {
      selectedDocument === null ?
        <DocumentSearch setSearching={setSearching} searching={searching}/>
      :
        <PageNotFoundError/>
    }
    {
      selectedDocument === null ?
        <DocumentSearchResults searching={searching}/>
      :
        <></>
    }
  </div>
}


export default Overlord;
