// React
import { useState, useRef, useEffect } from 'react';
// Assets
import './home.css';
import BannerImg from '../../assets/images/banner2.png';
import FacebookSVG from '../../shared/assets/icons/facebook.svg';
import TwitterSVG from '../../shared/assets/icons/twitter.svg';
import LinkedInSVG from '../../shared/assets/icons/linkedin.svg';
import GithubSVG from '../../shared/assets/icons/github.svg';
import OverlordImg from '../../assets/images/overlord.svg';
import DownloadImg from '../../assets/icons/download.svg';
import EnquireImg from '../../assets/icons/info.svg';
import BackgroundVideo from '../../assets/videos/code.mp4';
// Shared
import { setAppTitle } from '../../shared/components/routes/routes';


const Home = () => {
  const backgroundVideo = useRef<HTMLVideoElement>(null);
  setAppTitle("Home");

  useEffect(() => {
    if (backgroundVideo.current !== null) {
      //@ts-ignore
      if (document.webkitCancelFullscreen) {
        //@ts-ignore
        document.webkitCancelFullscreen();
      }
      backgroundVideo.current.onpause = (event:any) => {
        if (backgroundVideo.current !== null) backgroundVideo.current.play();
      }
    }
  }, [backgroundVideo.current?.onplaying])

  window.onload = () => {
    if (backgroundVideo.current !== null) {backgroundVideo.current.play();}
  }

  return <>
    <div className="home-background-video" style={{ zIndex: '-1', userSelect: 'none', pointerEvents: 'none' }}>
      <video ref={backgroundVideo} autoPlay muted loop playsInline>
        <source src={BackgroundVideo} type="video/mp4" />
      </video>
    </div>
    <div id="container" style={{
      position: 'fixed', zIndex: '1', width: '100vw', height: '100vh', overflow: 'hidden'
    }}>
      <Header/>
      <Content/>
      <Footer/>
    </div>
  </>
}


